#advantages {
  margin: 50px 0 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-image: url(images/eef44747120d9e05c427eadeece4b302.png);
  /* Set a specific height */
  height: 500px;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }
  #advantages .container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center; }
  #advantages .advantages_table {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 100%;
    flex-wrap: wrap; }
  #advantages .advantage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 4px solid #190223;
    padding: 20px;
    background-color: white;
    width: 200px;
    -webkit-transition: all 0.5s ease-in 0.0s;
    -moz-transition: all 0.5s ease-in 0.0s;
    -o-transition: all 0.5s ease-in 0.0s;
    transition: all 0.5s ease-in 0.0s; }
    #advantages .advantage .advantage_icon {
      font-size: 60px;
      color: #190223; }
    #advantages .advantage .advantage_description {
      text-transform: uppercase;
      font-family: LatoWebSemibold;
      font-size: 20px;
      color: #190223;
      text-align: center;
      line-height: 1.6; }
    #advantages .advantage:hover {
      background-color: #190223; }
      #advantages .advantage:hover .advantage_icon {
        color: white; }
      #advantages .advantage:hover .advantage_description {
        color: white; }
  @media only screen and (max-width: 767px) {
    #advantages {
      height: 720px; } }
  @media only screen and (max-width: 767px) and (orientation: landscape) {
    #advantages {
      height: 670px; } }
  @media only screen and (max-width: 767px) {
      #advantages .advantages_table {
        padding: 20px; }
      #advantages .advantage {
        padding: 20px;
        margin: 20px 10px;
        width: 100%; }
        #advantages .advantage .advantage_icon {
          font-size: 30px; }
        #advantages .advantage .advantage_description {
          font-size: 16px;
          line-height: 1.6; } }
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    #advantages {
      height: 750px;
      padding-top: 50px; }
      #advantages .advantage {
        height: 284px; } }
  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    #advantages .advantage {
      margin: 14px 20px !important;
      align-items: unset; }
      #advantages .advantage .advantage_icon {
        text-align: center; } }
