#about_description {
  display: flex;
  flex-direction: column;
  height: 750px;
  width: 100%; }
  #about_description .about_description_container {
    position: relative;
    width: 100%; }
  #about_description .about_description_img {
    position: absolute;
    top: 0;
    left: 0;
    height: 820px;
    width: 100%;
    background-image: url(images/48129f457580089761baa3d753aeff75.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.4; }
  #about_description .about_description_content {
    position: absolute;
    top: 0;
    left: 0;
    padding: 100px 50px;
    width: 75%;
    display: flex;
    flex-direction: column; }
    #about_description .about_description_content .description_content_part {
      display: flex;
      justify-content: space-between;
      align-items: center; }
      #about_description .about_description_content .description_content_part .description_content_part_img {
        font-size: 44px;
        color: #190223; }
      #about_description .about_description_content .description_content_part .description_content_part_text {
        font-family: LatoWebSemibold;
        font-size: 23px;
        width: 88%; }
        #about_description .about_description_content .description_content_part .description_content_part_text.odd {
          color: #190223; }
        #about_description .about_description_content .description_content_part .description_content_part_text.even {
          color: #a6a7b2; }
    #about_description .about_description_content:not(:hover) .even span, #about_description .about_description_content:not(:hover) .even p {
      -webkit-animation: about-text-color-animation-even 4s linear infinite alternate-reverse;
      -moz-animation: about-text-color-animation-even 4s linear infinite alternate-reverse;
      -o-animation: about-text-color-animation-even 4s linear infinite alternate-reverse; }
    #about_description .about_description_content:not(:hover) .odd span, #about_description .about_description_content:not(:hover) .odd p {
      -webkit-animation: about-text-color-animation-odd 4s linear infinite alternate-reverse;
      -moz-animation: about-text-color-animation-odd 4s linear infinite alternate-reverse;
      -o-animation: about-text-color-animation-odd 4s linear infinite alternate-reverse; }
  @media only screen and (max-width: 767px) {
    #about_description {
      height: 860px; } }
  @media only screen and (max-width: 767px) and (orientation: landscape) {
    #about_description {
      height: 670px; } }
  @media only screen and (max-width: 767px) {
      #about_description .about_description_img {
        background-size: contain; }
      #about_description .about_description_content {
        width: 100%;
        padding: 0; }
        #about_description .about_description_content .description_content_part_img {
          font-size: 25px !important; }
        #about_description .about_description_content .description_content_part_text {
          font-size: 18px !important;
          width: 100%; } }
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    #about_description {
      height: 860px; }
      #about_description .about_description_img {
        background-size: contain; }
      #about_description .about_description_content {
        width: 100%;
        padding: 0; }
        #about_description .about_description_content .description_content_part_text {
          width: 100%; } }
  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    #about_description {
      height: 800px; }
      #about_description .about_description_content {
        width: 80%;
        padding: 40px 20px; }
        #about_description .about_description_content .description_content_part_text {
          width: 100%; } }
