#introduction .introduction_content {
  display: flex;
  flex-direction: column;
  justify-content: center; }

#introduction .app_logo {
  height: 200px;
  background-image: url(images/415b0a7bf16f367e65f00689dd691131.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin-top: 60px; }

#introduction .description {
  font-family: LatoWebSemibold;
  font-size: 32px;
  color: #190223;
  text-align: center;
  line-height: 1.8;
  padding: 0 70px; }

@media only screen and (max-width: 767px) {
  #introduction .app_logo {
    height: 100px;
    margin-top: 30px; }
  #introduction .description {
    font-size: 18px;
    padding: 0 0; } }
