#offers_list .offers_table {
  border-top: 4px solid #190223;
  border-bottom: 2px solid #190223;
  margin: 50px 0 70px;
  padding-bottom: 5px; }

#offers_list .offer_row {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 40px 0;
  margin-top: 5px;
  border-top: 2px solid #190223;
  border-bottom: 4px solid #190223; }
  #offers_list .offer_row.odd {
    -webkit-flex-direction: row-reverse;
    flex-direction: row-reverse; }

#offers_list .offer_description {
  width: 67%; }

#offers_list .offer_photo_container {
  width: 300px;
  height: 230px;
  background-color: white;
  border: 5px dotted #190223;
  overflow: hidden;
  position: relative; }
  #offers_list .offer_photo_container:hover .offer_photo {
    -ms-transform: scale(1.5, 1.5);
    /* IE 9 */
    -webkit-transform: scale(1.5, 1.5);
    /* Safari */
    transform: scale(1.5, 1.5);
    opacity: 0.3; }
  #offers_list .offer_photo_container:hover .overflow_container {
    visibility: visible; }

#offers_list .offer_photo {
  width: 100%;
  height: 100%;
  background-color: white;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 0.5s ease; }

#offers_list .overflow_container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center; }

#offers_list .offer_name {
  font-size: 28px;
  text-transform: uppercase; }

#offers_list .offer_text {
  font-size: 20px;
  line-height: 1.5; }
  #offers_list .offer_text ul {
    list-style-type: square !important; }

@media only screen and (max-width: 767px) {
  #offers_list .offers_table {
    margin: 50px 0; }
  #offers_list .offer_row {
    -webkit-flex-direction: row !important;
    flex-direction: row !important; }
    #offers_list .offer_row .offer_photo_container {
      width: 230px;
      height: 170px; }
    #offers_list .offer_row .offer_description {
      width: 100%; }
      #offers_list .offer_row .offer_description .offer_name, #offers_list .offer_row .offer_description .offer_text {
        text-align: center; }
      #offers_list .offer_row .offer_description .offer_name {
        font-size: 20px;
        text-transform: uppercase; }
      #offers_list .offer_row .offer_description .offer_text {
        font-size: 16px; } }

@media only screen and (min-width: 768px) and (max-width: 991px) {
  #offers_list .offers_table {
    margin: 50px 0; }
  #offers_list .offer_row {
    -webkit-flex-direction: row !important;
    flex-direction: row !important; }
    #offers_list .offer_row .offer_description {
      width: 100%; }
      #offers_list .offer_row .offer_description .offer_name, #offers_list .offer_row .offer_description .offer_text {
        text-align: center; } }

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  #offers_list .offer_description {
