#products .container {
  display: flex;
  flex-direction: column;
  justify-content: center; }

#products .products_header {
  text-transform: uppercase;
  text-align: center;
  font-family: LatoWebSemibold; }

#products .lines_1 {
  height: 5px;
  width: 100%;
  border-top: 2px solid #190223;
  border-bottom: 4px solid #190223; }

#products .products_list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  height: 250px; }
  #products .products_list .product .product_icon_container {
    position: relative; }
  #products .products_list .product .product_icon_overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: LatoWebSemibold;
    text-transform: uppercase;
    color: #190223;
    line-height: 1.5; }
    #products .products_list .product .product_icon_overlay h4 {
      margin-bottom: 0; }
    #products .products_list .product .product_icon_overlay .arrow_go {
      font-size: 32px;
      color: #190223;
      -webkit-animation-name: arrow-animation;
      /* Safari 4.0 - 8.0 */
      -webkit-animation-duration: 1s;
      /* Safari 4.0 - 8.0 */
      -webkit-animation-iteration-count: infinite;
      /* Safari 4.0 - 8.0 */
      -webkit-animation-direction: alternate-reverse;
      /* Safari 4.0 - 8.0 */
      animation-name: arrow-animation;
      animation-duration: 1s;
      animation-iteration-count: infinite;
      animation-direction: alternate-reverse; }
  #products .products_list .product .product_icon_image {
    transition: all 0.5s ease;
    width: 150px;
    height: 150px; }
  #products .products_list .product:hover .product_icon_image {
    -ms-transform: scale(1.5, 1.5);
    /* IE 9 */
    -webkit-transform: scale(1.5, 1.5);
    /* Safari */
    transform: scale(1.5, 1.5);
    opacity: 0.3; }
  #products .products_list .product:hover .product_icon_overlay {
    visibility: visible; }

@media only screen and (max-width: 767px) {
  #products .products_header {
    font-size: 22px !important; }
  #products .products_list {
    height: 460px; } }
  @media only screen and (max-width: 767px) and (orientation: landscape) {
    #products .products_list {
      height: 320px; } }

@media only screen and (max-width: 767px) {
    #products .products_list .product_icon_image {
      width: 120px !important;
      height: 120px !important; } }

@media only screen and (min-width: 768px) and (max-width: 991px) {
  #products .products_list {
    height: 400px; } }
