#contact_form {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }
  #contact_form .form_title {
    margin: 0;
    line-height: 1.8;
    text-align: center;
    font-size: 20px; }
  #contact_form .form_fields {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 65%; }
  #contact_form .form_input {
    height: 62px;
    width: 100%; }
    #contact_form .form_input label {
      font-family: LatoWeb !important;
      color: #190223;
      font-size: 14px; }
      #contact_form .form_input label.error {
        color: red; }
    #contact_form .form_input p {
      display: none !important; }
  #contact_form .sendBtn {
    -webkit-transition: width 2.0s ease-in 0.0s;
    -moz-transition: width 2.0s ease-in 0.0s;
    -o-transition: width 2.0s ease-in 0.0s;
    transition: width 2.0s ease-in 0.0s; }
    #contact_form .sendBtn.clicked {
      border: 2px solid #190223;
      background-color: white;
      width: 350px;
      color: #190223 !important;
      display: flex; }
      #contact_form .sendBtn.clicked div {
        margin-left: 10px; }
    #contact_form .sendBtn.afterSend {
      background-color: #190223;
      color: white !important; }
  #contact_form .MuiInputBase-root {
    font-family: LatoWeb !important;
    color: #190223;
    font-size: 14px; }
    #contact_form .MuiInputBase-root:after {
      border-bottom-color: #42075b !important; }
    #contact_form .MuiInputBase-root:before {
      border-bottom-color: #42075b !important; }
    #contact_form .MuiInputBase-root.focused:after {
      border-bottom-color: #6a418f !important; }
    #contact_form .MuiInputBase-root.focused:before {
      border-bottom-color: #6a418f !important; }
    #contact_form .MuiInputBase-root.error:after {
      border-bottom-color: red !important; }
    #contact_form .MuiInputBase-root.error:after {
      border-bottom-color: red !important; }
  #contact_form .MuiInput-underline.Mui-error:after {
    transform: scaleX(1);
    border-bottom-color: red !important; }
  #contact_form .MuiFormLabel-root.Mui-error {
    color: red; }
  #contact_form input:-webkit-autofill,
  #contact_form input:-webkit-autofill:hover,
  #contact_form input:-webkit-autofill:focus,
  #contact_form textarea:-webkit-autofill,
  #contact_form textarea:-webkit-autofill:hover,
  #contact_form textarea:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0px 1000px #ffffff inset !important; }
  @media only screen and (max-width: 767px) {
    #contact_form {
      width: 100%;
      height: 550px; }
      #contact_form .form_fields {
        width: 75%; } }
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    #contact_form {
      width: 100%;
      height: 550px; }
      #contact_form .form_fields {
        width: 75%; } }
