#contact_page .contact_us {
  text-align: center;
  margin: 0 50px;
  -webkit-animation-name: contact-animation;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-duration: 2s;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-iteration-count: infinite;
  /* Safari 4.0 - 8.0 */
  -webkit-animation-direction: alternate-reverse;
  /* Safari 4.0 - 8.0 */
  animation-name: contact-animation;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-direction: alternate-reverse; }

#contact_page .contact_background {
  margin: 50px 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(images/eef44747120d9e05c427eadeece4b302.png);
  height: 700px;
  background-attachment: fixed;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 25px 25px 10px 0 white inset, -25px -25px 10px 0 white inset; }
  #contact_page .contact_background .container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center; }

@media only screen and (max-width: 767px) {
  #contact_page .contact_us {
    margin: 0 30px; }
  #contact_page .contact_background {
    margin: 30px 0;
    height: 1050px; } }

@media only screen and (min-width: 768px) and (max-width: 991px) {
  #contact_page .contact_us {
    margin-top: 60px; }
  #contact_page .contact_background {
    height: 1180px; } }
