#main_slider .slider_container {
  position: relative; }
  #main_slider .slider_container .title_container, #main_slider .slider_container .button_container {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  #main_slider .slider_container .title_container {
    top: 200px; }
  #main_slider .slider_container .button_container {
    bottom: 80px; }
  #main_slider .slider_container .button, #main_slider .slider_container .header_title {
    margin: 15px auto; }
  #main_slider .slider_container .header_title {
    text-align: center; }

#main_slider .slick-slider {
  height: 660px; }
  #main_slider .slick-slider .slider_photo1, #main_slider .slick-slider .slider_photo2, #main_slider .slick-slider .slider_photo3, #main_slider .slick-slider .slider_photo4 {
    height: 660px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    overflow-x: hidden;
    transition: all 0.5s ease-in-out; }
  #main_slider .slick-slider .slider_photo1 {
    background-image: url(images/eef44747120d9e05c427eadeece4b302.png); }
  #main_slider .slick-slider .slider_photo2 {
    background-image: url(images/87be07f1d9d7bdfadc2fc608c096979f.jpg); }
  #main_slider .slick-slider .slider_photo3 {
    background-image: url(images/d89aecbbee1c44d03f2d6ac6727499f1.jpg); }
  #main_slider .slick-slider .slider_photo4 {
    background-image: url(images/1c6e4a60f7c82a28812952e58076452f.jpg); }
  #main_slider .slick-slider .slick-active .slider_photo1, #main_slider .slick-slider .slick-active .slider_photo2, #main_slider .slick-slider .slick-active .slider_photo3, #main_slider .slick-slider .slick-active .slider_photo4 {
    -webkit-animation-name: slider-animation;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 4s;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-iteration-count: 1;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-direction: alternate;
    /* Safari 4.0 - 8.0 */
    animation-name: slider-animation;
    animation-duration: 4s;
    animation-iteration-count: 1;
    animation-direction: alternate; }
  #main_slider .slick-slider .slick-arrow::before {
    color: #190223;
    font-size: 40px; }
  #main_slider .slick-slider .slick-prev {
    left: 120px !important;
    z-index: 1; }
  #main_slider .slick-slider .slick-next {
    right: 120px !important; }
  #main_slider .slick-slider .slick-dots {
    bottom: 40px !important; }
    #main_slider .slick-slider .slick-dots li button::before {
      font-size: 16px !important;
      color: #190223; }

@media only screen and (max-width: 767px) {
  #main_slider .title_container {
    top: 120px !important; }
  #main_slider .button_container {
    bottom: 30px !important; }
  #main_slider .slick-slider {
    height: 400px !important; }
    #main_slider .slick-slider .slider_photo1, #main_slider .slick-slider .slider_photo2, #main_slider .slick-slider .slider_photo3, #main_slider .slick-slider .slider_photo4 {
      height: 400px !important; }
    #main_slider .slick-slider .slick-arrow::before {
      font-size: 25px !important; }
    #main_slider .slick-slider .slick-prev {
      left: 30px !important; }
    #main_slider .slick-slider .slick-next {
      right: 30px !important; }
    #main_slider .slick-slider .slick-dots {
      bottom: 10px !important; }
      #main_slider .slick-slider .slick-dots li button::before {
        font-size: 14px !important; } }

@media only screen and (min-width: 768px) and (max-width: 991px) {
  #main_slider .title_container {
    top: 210px !important; }
  #main_slider .slick-slider .slick-arrow::before {
    font-size: 50px !important; }
  #main_slider .slick-slider .slick-prev {
    left: 100px !important; }
  #main_slider .slick-slider .slick-next {
    right: 100px !important; } }
