#gallery_list .photos {
  display: -webkit-flex;
  /* Safari */
  -webkit-flex-wrap: wrap;
  /* Safari 6.1+ */
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 50px;
  justify-content: space-around; }
  #gallery_list .photos .photo_link {
    display: inline-block;
    margin-top: 50px; }
    @media only screen and (max-width: 767px) {
      #gallery_list .photos .photo_link {
        margin: 0 0 50px; } }
  #gallery_list .photos .photo_container {
    position: relative;
    width: 300px;
    height: 200px;
    overflow: hidden; }
    #gallery_list .photos .photo_container .photo {
      width: 100%;
      height: 100%;
      transition: 0.8s ease; }
    #gallery_list .photos .photo_container .overlay {
      position: absolute;
      bottom: 100%;
      left: 0;
      right: 0;
      background-color: rgba(25, 2, 35, 0.7);
      overflow: hidden;
      width: 100%;
      height: 0;
      transition: .5s ease; }
    #gallery_list .photos .photo_container:hover .photo {
      -ms-transform: scale(1.5, 1.5);
      /* IE 9 */
      -webkit-transform: scale(1.5, 1.5);
      /* Safari */
      transform: scale(1.5, 1.5); }
    #gallery_list .photos .photo_container:hover .overlay {
      bottom: 0;
      height: 100%; }
    #gallery_list .photos .photo_container .text {
      color: white;
      font-size: 20px;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      text-align: center; }
