#contact_info {
  width: 50%;
  height: 100%;
  background-color: #190223;
  color: white;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }
  #contact_info p {
    margin-left: 40px;
    line-height: 1.7; }
  #contact_info a {
    color: white !important; }
  #contact_info span {
    margin-right: 10px; }
  @media only screen and (max-width: 767px) {
    #contact_info {
      width: 100%;
      height: 400px; }
      #contact_info p {
        margin-left: 20px;
        font-size: 16px; } }
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    #contact_info {
      width: 100%;
      height: 450px; }
      #contact_info p {
        margin-left: 20px; } }
