#contact_container {
  background-color: white;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.23), 0 3px 10px rgba(0, 0, 0, 0.16) !important;
  width: 75%;
  height: 550px;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap; }
  @media only screen and (max-width: 767px) {
    #contact_container {
      width: 100%;
      height: 950px;
      margin-top: 50px; } }
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    #contact_container {
      width: 80%;
      height: 1000px;
      margin-top: 50px; } }
