#page_footer {
  width: 100%;
  height: 130px;
  background-color: #190223;
  display: flex;
  align-items: center; }
  #page_footer .container {
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }
    #page_footer .container .copyright {
      font-size: 16px;
      color: white; }
    #page_footer .container .footer_contact {
      height: 100%;
      font-size: 16px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end; }
      #page_footer .container .footer_contact a {
        color: white;
        line-height: 2.0; }
        #page_footer .container .footer_contact a span {
          margin-right: 10px; }
        #page_footer .container .footer_contact a:hover {
          font-family: LatoWebBold; }
    @media only screen and (max-width: 767px) {
      #page_footer .container .copyright, #page_footer .container .footer_contact {
        font-size: 12px; } }
  @media only screen and (max-width: 767px) {
    #page_footer {
      height: 100px; } }
