@-webkit-keyframes stamp-animation {
  0% {
    color: #6a418f;
    -webkit-transform: rotateY(0deg); }
  100% {
    color: #190223;
    -webkit-transform: rotateY(180deg); } }

/* Standard syntax */
@keyframes stamp-animation {
  0% {
    color: #6a418f;
    transform: rotateY(0deg); }
  100% {
    color: #190223;
    transform: rotateY(180deg); } }

/* Standard syntax */
@keyframes slider-animation {
  0% {
    transform: scale(1, 1); }
  50% {
    transform: scale(1.07, 1.07); }
  60% {
    transform: scale(1.07, 1.07); }
  100% {
    transform: scale(1, 1); } }

@-webkit-keyframes slider-animation {
  0% {
    -webkit-transform: scale(1, 1); }
  50% {
    -webkit-transform: scale(1.07, 1.07); }
  60% {
    -webkit-transform: scale(1.07, 1.07); }
  100% {
    -webkit-transform: scale(1, 1); } }

/* Standard syntax */
@keyframes arrow-animation {
  0% {
    transform: translate(-20px, 0); }
  100% {
    transform: translate(20px, 0); } }

@-webkit-keyframes arrow-animation {
  0% {
    -webkit-transform: translate(-20px, 0); }
  100% {
    -webkit-transform: translate(20px, 0); } }

@keyframes about-text-color-animation-even {
  0% {
    color: #a6a7b2; }
  50% {
    color: #190223; }
  100% {
    color: #190223; } }

@-webkit-keyframes about-text-color-animation-even {
  0% {
    color: #a6a7b2; }
  50% {
    color: #190223; }
  100% {
    color: #190223; } }

@keyframes about-text-color-animation-odd {
  0% {
    color: #190223; }
  50% {
    color: #190223; }
  100% {
    color: #a6a7b2; } }

@-webkit-keyframes about-text-color-animation-odd {
  0% {
    color: #190223; }
  50% {
    color: #190223; }
  100% {
    color: #a6a7b2; } }

/* Standard syntax */
@keyframes contact-animation {
  0% {
    transform: scale(1, 1); }
  100% {
    transform: scale(1.3, 1.3); } }

@-webkit-keyframes contact-animation {
  0% {
    -webkit-transform: scale(1, 1); }
  100% {
    -webkit-transform: scale(1.3, 1.3); } }

html {
  overflow-x: hidden !important; }
  html body {
    font-family: LatoWeb !important;
    font-size: 14px;
    margin: 0 !important;
    overflow-x: hidden !important;
    padding-top: 0; }
  html .app_content {
    min-height: 100vh;
    overflow-x: hidden !important; }
  html #about_page, html #offer_page, html #gallery_page, html #contact_page {
    padding-top: 150px; }
  html .container {
    width: 1152px;
    margin: 0 auto !important;
    min-height: 100%; }
    @media only screen and (max-width: 767px) {
      html .container {
        width: 300px; } }
  @media only screen and (max-width: 767px) and (orientation: landscape) {
    html .container {
      width: 440px; } }
    @media only screen and (min-width: 768px) and (max-width: 991px) {
      html .container {
        width: 660px; } }
    @media only screen and (min-width: 992px) and (max-width: 1199px) {
      html .container {
        width: 920px; } }
  html .page_container {
    overflow-y: hidden !important;
    overflow-x: hidden !important; }
  html .header_container {
    width: 1152px;
    margin: 0 auto !important;
    min-height: 100%; }
    @media only screen and (max-width: 767px) {
      html .header_container {
        width: 100%;
        padding: 0 20px 0 20px; } }
    @media only screen and (min-width: 768px) and (max-width: 991px) {
      html .header_container {
        width: 660px; } }
    @media only screen and (min-width: 992px) and (max-width: 1199px) {
      html .header_container {
        width: 920px; } }
  html ul {
    list-style: none !important; }
  html a {
    text-decoration: none !important; }
  html .button {
    background-color: #190223;
    border: none;
    font-family: LatoWebSemibold !important;
    color: white !important;
    padding: 20px 40px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
    margin: 4px 2px;
    cursor: pointer;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.23), 0 3px 10px rgba(0, 0, 0, 0.16) !important; }
    html .button a {
      color: white !important;
      cursor: pointer; }
    html .button:hover {
      background-color: #6a418f; }
    html .button:disabled {
      background-color: #a6a7b2;
      cursor: auto; }
  html button, html .button, html a {
    text-align: center;
    -webkit-tap-highlight-color: transparent;
    appearance: none;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
    outline: none; }
  html .button a {
    text-transform: uppercase; }
  html h1 {
    font-size: 60px;
    color: #190223; }
  html h2 {
    font-size: 36px;
    color: #190223; }
  html h3 {
    font-size: 33px;
    color: #190223; }
  html h4 {
    font-size: 23px; }
  html h5 {
    font-size: 14px;
    color: #190223; }
  html .page_title {
    text-transform: uppercase;
    margin-left: 50px; }
  html .stamp {
    display: inline-block;
    color: #190223;
    font-size: 50px;
    text-align: center;
    margin: 50px 0 0;
    width: 100%;
    -webkit-animation-name: stamp-animation;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 3s;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-iteration-count: infinite;
    /* Safari 4.0 - 8.0 */
    -webkit-animation-direction: alternate-reverse;
    /* Safari 4.0 - 8.0 */
    animation-name: stamp-animation;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse; }
  html #lightbox {
    display: flex;
    flex-direction: column-reverse; }
    html #lightbox .lb-dataContainer {
      margin-top: -47px; }
  html .main-loader {
    display: block;
    margin: 260px auto;
    border-color: #190223; }
  @media only screen and (max-width: 767px) {
    html #about_page, html #offer_page, html #gallery_page, html #contact_page {
      padding-top: 130px; }
    html .page_title {
      margin-left: 20px; }
    html .button {
      padding: 16px 28px;
      font-size: 14px; }
    html h1 {
      font-size: 36px; }
    html h2 {
      font-size: 20px; }
    html h3 {
      font-size: 18px; }
    html h4 {
      font-size: 16px; }
    html .stamp {
      font-size: 30px;
      margin: 16px 0 0; } }
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    html #about_page, html #offer_page, html #gallery_page, html #contact_page {
      padding-top: 180px; }
    html .stamp {
      margin: 10px 0 0; } }
