#page_header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: 120px;
  background-color: transparent;
  display: flex;
  align-items: center;
  -webkit-transition: all 0.5s ease-in-out 0.0s;
  -moz-transition: all 0.5s ease-in-out 0.0s;
  -o-transition: all 0.5s ease-in-out 0.0s;
  transition: all 0.5s ease-in-out 0.0s; }
  #page_header.header--scrolled {
    border-bottom: 1px solid #190223;
    background-color: white;
    height: 90px; }
  #page_header .site-logo, #page_header .site-logo a {
    display: flex;
    align-items: center; }
  #page_header .site-logo {
    margin: 0; }
    #page_header .site-logo img {
      max-width: 205px;
      min-width: 165px;
      width: calc(100px + 10vw);
      backface-visibility: hidden; }
  #page_header .navbar {
    width: 100%;
    display: flex; }
    #page_header .navbar .navbar__content {
      display: flex;
      justify-content: space-between; }
    #page_header .navbar .navbar__hamburger {
      display: none;
      background: 0 0;
      border: 2px solid #190223;
      height: 32px;
      width: 32px;
      padding: 0;
      margin-right: 30px; }
      #page_header .navbar .navbar__hamburger span {
        display: block;
        border-bottom: 2px solid #190223;
        width: 65%;
        margin: 0 auto;
        transform-origin: center;
        transition: .3s ease-in-out; }
        #page_header .navbar .navbar__hamburger span:nth-child(1) {
          margin-bottom: 5px; }
        #page_header .navbar .navbar__hamburger span:nth-child(3) {
          margin-top: 5px; }
      #page_header .navbar .navbar__hamburger.hamburger--opened span:nth-child(1) {
        transform: translateY(7px) rotate(135deg); }
      #page_header .navbar .navbar__hamburger.hamburger--opened span:nth-child(2) {
        transform: rotate(90deg) scale(0); }
      #page_header .navbar .navbar__hamburger.hamburger--opened span:nth-child(3) {
        transform: translateY(-7px) rotate(-135deg); }
    #page_header .navbar .navbar__links {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 10px 0; }
    #page_header .navbar .navbar__contact, #page_header .navbar .site-nav__list {
      display: flex;
      padding: 0;
      margin: 0; }
    #page_header .navbar .navbar__contact--mobile {
      display: none; }
    #page_header .navbar .navbar__contact {
      justify-content: space-around; }
      #page_header .navbar .navbar__contact a {
        padding: 10px 0;
        font-size: 12px;
        color: #190223; }
        #page_header .navbar .navbar__contact a span {
          color: #190223;
          margin-right: 10px; }
        #page_header .navbar .navbar__contact a:hover {
          color: #6a418f;
          font-family: LatoWebBlack; }
          #page_header .navbar .navbar__contact a:hover span {
            color: #6a418f; }
    #page_header .navbar .site-nav__list {
      justify-content: flex-end; }
    #page_header .navbar .site-nav__link {
      padding: 10px;
      text-transform: uppercase;
      font-family: LatoWebSemibold;
      font-size: 16px;
      color: #190223; }
      #page_header .navbar .site-nav__link:hover, #page_header .navbar .site-nav__link.current {
        color: #6a418f;
        font-family: LatoWebBlack; }
  @media only screen and (max-width: 767px) {
    #page_header .navbar__contact--mobile {
      display: flex !important;
      margin: -15px 0; }
    #page_header .navbar__hamburger {
      display: flex !important;
      flex-direction: column;
      align-items: stretch;
      justify-content: center;
      align-self: center; }
    #page_header.header--expanded {
      border-bottom: 1px solid #190223;
      background-color: white; }
      #page_header.header--expanded .site-nav {
        max-height: 20.5rem;
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        background-color: white;
        border-top: 1px solid #190223; }
    #page_header.header--scrolled {
      height: 120px; }
    #page_header .site-nav, #page_header .site-nav__item, #page_header .site-nav__list {
      width: 100%; }
    #page_header .site-nav {
      position: absolute;
      top: 100%;
      right: 0;
      left: 0;
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      visibility: hidden;
      pointer-events: none;
      transition: all .5s ease-in-out;
      transition-delay: .1s; }
      #page_header .site-nav .navbar__contact {
        display: none !important; }
    #page_header .site-nav__list {
      flex-direction: column;
      padding: 10px 0; }
    #page_header .site-nav__item {
      padding: 15px 20px; }
      #page_header .site-nav__item a {
        font-size: 14px; } }
  @media only screen and (min-width: 768px) and (max-width: 991px) {
    #page_header {
      height: 160px; }
      #page_header .site-logo img {
        max-width: 300px !important;
        width: 240px !important; }
      #page_header .navbar__contact--mobile {
        display: flex !important;
        justify-content: space-between !important;
        margin: 35px 0; }
        #page_header .navbar__contact--mobile a {
          font-size: 14px !important; }
      #page_header .navbar__hamburger {
        display: flex !important;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;
        align-self: center;
        width: 50px !important;
        height: 50px !important; }
      #page_header.header--expanded {
        border-bottom: 1px solid #190223;
        background-color: white; }
        #page_header.header--expanded .site-nav {
          max-height: 20.5rem;
          opacity: 1;
          visibility: visible;
          pointer-events: all;
          background-color: white;
          border-top: 1px solid #190223; }
      #page_header.header--scrolled {
        height: 180px !important; }
      #page_header .site-nav, #page_header .site-nav__item, #page_header .site-nav__list {
        width: 100%; }
      #page_header .site-nav {
        position: absolute;
        top: 100%;
        right: 0;
        left: 0;
        max-height: 0;
        opacity: 0;
        overflow: hidden;
        visibility: hidden;
        pointer-events: none;
        transition: all .5s ease-in-out;
        transition-delay: .1s; }
        #page_header .site-nav .navbar__contact {
          display: none !important; }
      #page_header .site-nav__list {
        flex-direction: column;
        padding: 10px 0; }
      #page_header .site-nav__item {
        padding: 15px 20px !important; }
        #page_header .site-nav__item a {
          font-size: 18px !important; } }
